import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			user_id: 0,
			applicationData: {
				id: 0,
				created_at: "",
				name: "",
				gender: "",
				phone: "",
				email: "",
				ip: "",
				idc_front_pic_url: "",
				idc_back_pic_url: "",
				reason_of_refusal: null,
				flags: 0,
				user: 0
			},
			manageType: 'edit'
		}
	},
	onLoad(option) {
		let title = '新增社区申请';
		this.user_id = uni.getStorageSync("userDetail").id;
		if (option.type === 'edit') {
			title = '编辑申请';
			console.log(option);
			this.applicationData = JSON.parse(decodeURIComponent(option.data));
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {

		//提交
		confirm() {
			let data = this.applicationData;
			//验证姓名
			if (!data.name) {
				uni.showModal({
					title: '提示',
					content: '请填写姓名',
					success: function(res) {}
				});
				return;
			}
			//验证电话号码
			if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)) {
				uni.showModal({
					title: '提示',
					content: '请输入正确的手机号码',
					success: function(res) {}
				});
				return;
			}
			//验证编辑模式
			let that = this;
			if (that.manageType == "edit") {
				that.util.request(that.api.ApiRoot + "users/" + this.user_id + "/applications/" + this.applicationData.id + "/",
					data, 'PUT').then(function(res) {
					uni.showToast({
						title: res.errMsg,
						duration: 2000
					});
					uni.navigateBack();
				})

			} else {
				//发送请求
				that.util.request(that.api.ApiRoot + "users/" + this.user_id + "/applications/", data, 'POST').then(function(res) {
					uni.showToast({
						title: res.errMsg,
						duration: 2000
					});
					uni.navigateBack();
				})
			}
		},
		navTo(res) {
			uni.navigateTo({
				url: res
			})
		},
	}
}
